import React from 'react';
import './PlayerBubble.css';
import carImage from './Assets/car.png'; // путь к изображению машины

// Мемоизация компонента для предотвращения лишних перерендеров
const PlayerBubble = React.memo(({ x, y }) => {
  return (
    <img
      src={carImage}
      alt="Player Car"
      className="player-car"
      style={{ left: x, top: y }}
      
    />
  );
});

export default PlayerBubble;
